<template>
  <tr>
    <!-- Resource Selection Checkbox -->
    <td
      v-if="!isSorting"
      :class="{
        'w-16' : shouldShowCheckboxes || isSorting,
        'w-8' : !shouldShowCheckboxes
    }">
      <checkbox
        v-if="shouldShowCheckboxes"
        :data-testid="`${testId}-checkbox`"
        :dusk="`${resource['id'].value}-checkbox`"
      />
    </td>
    <!-- Sort Handle -->
    <td
      v-if="isSorting"
      class="td-fit align-center">
      <icon
        type="drag"
        view-box="0 0 24 24"
        width="30"
        height="30"
        class="drag-handle pt-3"/>
    </td>

    <!-- Fields -->
    <td
      v-for="field in resource.fields"
      :key="field.name">
      <component
        :is="'index-' + field.component"
        :class="`text-${field.text_align}`"
        :resource-name="resourceName"
        :field="field"
      />
    </td>
    <td
      v-if="isSorting"
      class="td-fit text-right pr-6"/>
    <td
      v-else
      class="td-fit text-right pr-6">
      <!-- View Resource Link -->
      <span>
        <router-link
          :to="{ name: 'detail', params: {
            resourceName: resourceName,
            resourceId: resourceId
          }}"
          title="Show"
          class="cursor-pointer text-70 hover:text-primary mr-3"
        >
          <icon
            type="view"
            width="22"
            height="18"
            view-box="0 0 22 16" />
        </router-link>
      </span>
      <span>
        <!-- Edit Resource Link -->
        <router-link
          :to="{ name: 'edit', params: {
            resourceName: resourceName,
            resourceId: resourceId
          }}"
          title="Edit"
          class="cursor-pointer text-70 hover:text-primary mr-3"
        >
          <icon type="edit" />
        </router-link>
      </span>
      <!-- Delete Resource Link -->
      <button
        class="appearance-none cursor-pointer text-70 hover:text-danger mr-3"
        title="Delete"
        @click.prevent="openDeleteModal"
      >
        <icon type="delete"/>
      </button>
      <portal to="modals">
        <transition name="fade">
          <delete-resource-modal
            v-if="deleteModalOpen"
            mode="delete"
            @confirm="confirmDelete"
            @close="closeDeleteModal"
          >
            <div
              slot-scope="{ uppercaseMode, mode }"
              class="p-8">
              <heading
                :level="2"
                class="mb-6">{{ uppercaseMode }}  Resource</heading>
              <p class="text-80 leading-normal">Are you sure you want to delete this resource?</p>
            </div>
          </delete-resource-modal>
        </transition>
      </portal>
    </td>
  </tr>
</template>

<script>
import { Deleteable } from "@/mixins";
export default {
  mixins: [Deleteable],
  props: {
    deleteResource: {
      type: Function,
      required: true
    },
    resource: {
      type: Object,
      required: true
    },
    resourceName: {
      type: String,
      required: true
    },
    relationshipType: {
      type: String,
      default: null
    },
    viaRelationship: {
      type: String,
      default: null
    },
    viaResource: {
      type: String,
      default: null
    },
    viaResourceId: {
      type: String,
      default: null
    },
    viaManyToMany: {
      type: Boolean,
      default: false
    },
    checked: {
      type: Boolean,
      default: false
    },
    actionsAreAvailable: {
      type: Boolean,
      default: false
    },
    shouldShowCheckboxes: {
      type: Boolean,
      default: false
    },
    isSorting: {
      type: Boolean,
      default: false
    }
  },

  data: () => ({
    deleteModalOpen: false
  }),

  computed: {
    resourceId() {
      return this.resource.id;
    }
  },

  methods: {
    openDeleteModal() {
      this.deleteModalOpen = true;
    },

    confirmDelete() {
      this.deleteResource(this.resource);
      this.closeDeleteModal();
    },

    closeDeleteModal() {
      this.deleteModalOpen = false;
    }
  }
};
</script>
