<template>
  <div>
    <slot>
      <h4 class="mb-3 text-90 font-normal text-lg">{{ panel.name }}</h4>
    </slot>

    <card class="mb-6 py-3 px-6">
      <component
        v-for="(field, index) in panel.fields"
        :class="{'remove-bottom-border': index == panel.fields.length - 1}"
        :key="index"
        :is="resolveComponentName(field)"
        :resource-name="resourceName"
        :resource-id="resourceId"
        :resource="resource"
        :field="field"
      />
    </card>
  </div>
</template>

<script>
import { BehavesAsPanel } from "@/mixins";

export default {
  mixins: [BehavesAsPanel],

  methods: {
    /**
     * Resolve the component name.
     */
    resolveComponentName(field) {
      return field.prefix_component
        ? "detail-" + field.component
        : field.component;
    }
  }
};
</script>
